import React, { useMemo, useState } from 'react';
import { Link } from 'react-router-dom';

const lines = [
  [0,1,2],[3,4,5],[6,7,8], // rows
  [0,3,6],[1,4,7],[2,5,8], // cols
  [0,4,8],[2,4,6],         // diagonals
];

function winnerOf(board) {
  for (const [a,b,c] of lines) {
    if (board[a] && board[a] === board[b] && board[a] === board[c]) return board[a];
  }
  return null;
}

export default function GameTicTacToe() {
  const [board, setBoard] = useState(Array(9).fill(null));
  const [xTurn, setXTurn] = useState(true);

  const winner = useMemo(() => winnerOf(board), [board]);
  const isDraw = useMemo(() => !winner && board.every(Boolean), [winner, board]);

  const play = (idx) => {
    if (winner || isDraw || board[idx]) return;
    const next = board.slice();
    next[idx] = xTurn ? 'X' : 'O';
    setBoard(next);
    setXTurn(!xTurn);
  };

  const reset = () => {
    setBoard(Array(9).fill(null));
    setXTurn(true);
  };

  return (
    <div className="card">
      <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginBottom: 8 }}>
        <h2 style={{ margin: 0 }}>Morpion</h2>
        <Link className="week-btn" to="/games">â†© Retour</Link>
      </div>
      <div style={{ marginBottom: 8, color: 'var(--muted)' }}>
        {winner ? `Vainqueur: ${winner}` : isDraw ? 'Match nul' : `Tour: ${xTurn ? 'X' : 'O'}`}
      </div>
      <div
        role="grid"
        style={{
          display: 'grid',
          gridTemplateColumns: 'repeat(3, 100px)',
          gridTemplateRows: 'repeat(3, 100px)',
          gap: 6,
          marginBottom: 10,
        }}
      >
        {board.map((v, i) => (
          <button
            key={i}
            role="gridcell"
            aria-label={`Case ${i + 1}`}
            onClick={() => play(i)}
            className="week-btn"
            style={{
              width: 100,
              height: 100,
              fontSize: 36,
              fontWeight: 800,
              background: v ? (v === 'X' ? '#2563eb' : '#10b981') : 'var(--panel)',
              borderColor: v ? 'transparent' : 'var(--line)',
              color: v ? '#fff' : 'var(--text)',
            }}
          >
            {v || ''}
          </button>
        ))}
      </div>
      <button className="week-btn" onClick={reset}>Recommencer</button>
    </div>
  );
}